import React, { useState, useRef } from "react";
import style from "./Accordian.module.css";

const Accordian = (props) => {
  const [setActive, setActiveState] = useState("");
  const [setHeight, setHeightState] = useState("0px");
  const [setRotate, setRotateState] = useState("accordion__icon");

  const content = useRef(null);

  function toggleAccordion() {
    setActiveState(setActive === "" ? "active" : "");
    setHeightState(
      setActive === "active" ? "0px" : `${content.current.scrollHeight}px`
    );
    setRotateState(
      setActive === "active" ? "accordion__icon" : "accordion__icon rotate"
    );
  }

  return (
    <div className={style["accordion__section"]}>
      <div className={`${style["accordion"]} ${style[setActive]}`}>
        <p className={style["accordion__title"]}>{props.title}</p>
        <div style={{ display: "flex" }}>
          <div onClick={toggleAccordion} className={style.button2}>
            {setActive === "active" ? "Hide Details" : "Show Details"}
          </div>
          <div
            className={style.button1}
            onClick={() => {
              props.handleSelection(props.index);
              //props.history.push(`/home?type=phc&option=${props.index}#about`);
            }}
          >
            {props.options.length > 0
              ? props.options.includes(props.index)
                ? "Remove"
                : "Add"
              : "Book now"}
          </div>
        </div>
      </div>
      <div
        ref={content}
        style={{
          maxHeight: `${setHeight}`,
          height: "300px",
          overflowY: "auto",
        }}
        className={style["accordion__content"]}
      >
        <div
          className={style["accordion__text"]}
          dangerouslySetInnerHTML={{ __html: props.content }}
        />
      </div>
    </div>
  );
};

export default Accordian;
