import React, { useState, useEffect } from "react";
import style from "./About.module.css";
import contactImg from "../../utils/contact.png";
import { hbcList, phcMainList } from "../Package/PackageDetails";
import { Multiselect } from "multiselect-react-dropdown";
import Package from "./../Package/Package";
import ReCAPTCHA from "react-google-recaptcha";
const About = ({ location }) => {
  const [payload, setPayload] = useState({
    first_name: "",
    last_name: "",
    email: "",
    phone: "",
    notes: "",
    service: "1",
    package: [],
  });
  const [preSelect, setPreselect] = useState([]);
  const [errorObj, setErrorObj] = useState({
    first_name: null,
    phone: null,
  });
  const [captchaFlag, setCaptcha] = useState(false);
  const [appmtFlag, setAppmtFlag] = useState(false);
  const handleUserInput = (key, value) => {
    setPayload(Object.assign({}, payload, { [key]: value }));
  };
  const populateMultiSelect = () => {
    let returnData = [];
    if (payload.service == 4) {
      return hbcList.map((data, index) => {
        return { name: data, id: index };
      });
    } else {
      return phcMainList.map((data, index) => {
        return { name: data, id: index };
      });
    }
  };
  useEffect(() => {
    // console.log(location.state);
    if (location.state && location.state.userData) {
      document.getElementById("about").scrollIntoView();
      let packages = location.state.userData.options;
      let service = location.state.userData.type === 1 ? "4" : "5";
      let preSelect = packages.map((id) => {
        return { id, name: service == "4" ? hbcList[id] : phcMainList[id] };
      });
      setPayload(
        Object.assign({}, payload, {
          package: packages.map((index) => {
            return service == "4" ? hbcList[index] : phcMainList[index];
          }),
          service,
        })
      );
      setPreselect(Object.assign({}, preSelect));
    }
  }, []);
  useEffect(() => {}, []);
  // useEffect(() => {
  //   let serviceType = params.get("type");
  //   let packageNum = params.get("option");
  //   if (serviceType != null && serviceType != undefined) {
  //     if (serviceType === "hbc" || serviceType === "phc") {
  //       let packageName =
  //         serviceType === "hbc" ? hbcList[packageNum] : phcMainList[packageNum];
  //       setPayload(Object.assign({}, payload, { package: packageName }));
  //     } else {
  //       setPayload(Object.assign({}, payload, { service: serviceType }));
  //     }
  //   }
  // }, [params.get("type")]);
  const formValidator = () => {
    let flag = true;
    let error = {
      first_name: null,
      phone: null,
    };
    if (payload.first_name.trim().length < 3) {
      error.first_name = "Please Enter a valid first name";
      flag = false;
    }
    if (payload.phone.trim().length != 10) {
      error.phone = "Please Enter a valid phone number";
      flag = false;
    }
    setErrorObj(Object.assign({}, error));
    return flag;
  };
  const handleSubmit = () => {
    if (formValidator()) {
      fetch("/sendmail", {
        method: "post",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      })
        .then((res) => {
          setAppmtFlag(true);
          setTimeout(function () {
            setAppmtFlag(false);
            setPayload(
              Object.assign({
                first_name: "",
                last_name: "",
                email: "",
                phone: "",
                notes: "",
                service: "1",
                package: [],
              })
            );
          }, 3000);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };
  return (
    <div className={style.container}>
      <img src={contactImg} className={style.aboutimg} />
      {appmtFlag ? (
        <div className={style.form1}>
          <div> Appointment Booked</div>
          <div>
            <lottie-player
              src="https://assets10.lottiefiles.com/packages/lf20_oMkAre.json"
              background="transparent"
              speed="1"
              style={{ width: "300px", height: "300px" }}
              loop
              autoplay
            ></lottie-player>
          </div>
        </div>
      ) : (
        <div className={style.form}>
          <h1>Fill your details</h1>
          <div className={style.field}>
            <label>First Name</label>
            <input
              type="text"
              value={payload.first_name}
              onChange={(e) => {
                handleUserInput("first_name", e.currentTarget.value);
              }}
              className={
                errorObj.first_name == null ? "" : style["input--error"]
              }
            />
            {errorObj.first_name && (
              <span className={style.errortext}>{errorObj.first_name}</span>
            )}
          </div>
          <div className={style.field}>
            <label>Last Name</label>
            <input
              type="text"
              value={payload.last_name}
              onChange={(e) => {
                handleUserInput("last_name", e.currentTarget.value);
              }}
              required={true}
            />
          </div>
          <div className={style.field}>
            <label>Email</label>
            <input
              type="text"
              value={payload.email}
              onChange={(e) => {
                handleUserInput("email", e.currentTarget.value);
              }}
            />
          </div>
          <div className={style.field}>
            <label>Phone</label>
            <input
              type="text"
              value={payload.phone}
              onChange={(e) => {
                handleUserInput("phone", e.currentTarget.value);
              }}
              className={errorObj.phone == null ? "" : style["input--error"]}
            />
            {errorObj.phone && (
              <span className={style.errortext}>{errorObj.phone}</span>
            )}
          </div>
          <div className={style.field}>
            <label>Additional Info/ Feedback</label>
            <input
              type="text"
              value={payload.notes}
              onChange={(e) => {
                handleUserInput("notes", e.currentTarget.value);
              }}
            />
          </div>
          <div className={style.field}>
            <label>Service</label>
            <select
              onChange={(e) => {
                handleUserInput("service", e.currentTarget.value);
              }}
              value={payload.service}
            >
              <option value="1">Consulting</option>
              <option value="2">Home Service(Lab Test)</option>
              <option value="3">Home Service(Doctor Visit)</option>
              <option value="4">Home Blood Collection</option>
              <option value="5">Preventive Health Checkup</option>
            </select>
            <a href="/package" target="_blank">
              need more details about available test/checkups?
            </a>
          </div>
          {payload.package.length > 0 ||
          payload.service == 4 ||
          payload.service == 5 ? (
            <div className={style.field}>
              <label>Package</label>
              <Multiselect
                options={populateMultiSelect()}
                selectedValues={preSelect}
                style={{
                  multiselectContainer: { width: "300px" },

                  searchBox: { background: "white" },
                  chips: {
                    background: "#E8505B",
                    textOverflow: "ellipsis",
                    whiteSpace: "nowrap",
                    maxWidth: "280px",
                    overflow: "hidden",
                    display: "inline-block",
                  },
                }}
                displayValue="name"
                onSelect={(data) => {
                  let packageName = data.map((obj) => obj.name);
                  setPayload(
                    Object.assign({}, payload, { package: packageName })
                  );
                }}
                onRemove={(data) => {
                  let packageName = data.map((obj) => obj.name);
                  setPayload(
                    Object.assign({}, payload, { package: packageName })
                  );
                }}
              />
              {/* <input
              type="text"
              value={payload.package}
              className={style["input--disabled"]}
            /> */}
            </div>
          ) : null}
          <ReCAPTCHA
            sitekey="6LepTywaAAAAALsNXsb37vZQTu-2H1PgabEbUyH7"
            onChange={(e) => {
              setCaptcha(true);
            }}
          />
          ,
          <div
            className={captchaFlag ? style.button : style["button--disabled"]}
            onClick={handleSubmit}
          >
            Book Appointment
          </div>
        </div>
      )}

      <div className={style.groupcontact}>
        <div className={style.contact}>
          <div>
            <h1>Contact us</h1>
            <span>
              044-48557830/ 8015162649/ 7550187731 <br />{" "}
              healthpluscenter2016@gmail.com
            </span>
          </div>
          <div>
            <h1>Address</h1>
            <span>
              No 26, Sumeru square, Kanaka Nagar, <br /> Agaram Main road ,
              Selaiyur,
              <br />
              Chennai -600073
            </span>
          </div>
        </div>
        <div className={style.map}>
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3889.0473547178653!2d80.13993111482132!3d12.904676590899752!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zMTLCsDU0JzE2LjgiTiA4MMKwMDgnMzEuNiJF!5e0!3m2!1sen!2sin!4v1602968141843!5m2!1sen!2sin"
            frameBorder="0"
            style={{ border: 0 }}
            allowFullScreen=""
            aria-hidden="false"
            tabIndex="0"
          ></iframe>
        </div>
      </div>
    </div>
  );
};

export default About;
