import React, { useState } from "react";
import style from "./HBC.module.css";
import { hbcList } from "../PackageDetails";

const HBC = ({ history, handleSelection, options }) => {
  const [searchText, setSearchText] = useState("");
  //const [options, setOptions] = useState([]);
  return (
    <div className={style.container}>
      <input
        type="text"
        className={style["input_search"]}
        onChange={(e) => {
          setSearchText(e.currentTarget.value);
        }}
        placeholder="Enter text to search"
      />
      <p className={style.addtionalinfo}>
        not able to find the test you are looking for?
      </p>
      <p className={style.addtionalinfo}>
        ADD THEM IN <b>ADDITIONAL INFO</b> WHILE BOOKING
      </p>
      <table>
        <tbody>
          {hbcList.map((data, i) => {
            if (
              searchText.trim().length > 0 &&
              data.toLowerCase().includes(searchText.toLowerCase())
            ) {
              return (
                <tr key={i}>
                  <td>{data}</td>
                  <td>
                    <div
                      className={style.button2}
                      onClick={() => {
                        handleSelection(i);

                        //history.push(`/home?type=hbc&option=${i}#about`);
                      }}
                    >
                      {options.length > 0
                        ? options.includes(i)
                          ? "Remove"
                          : "Add"
                        : "Book now"}
                    </div>
                  </td>
                </tr>
              );
            } else if (searchText.trim().length <= 0) {
              return (
                <tr key={i}>
                  <td>{data}</td>
                  <td>
                    <div
                      className={style.button2}
                      onClick={() => {
                        handleSelection(i);
                      }}
                    >
                      {options.length > 0
                        ? options.includes(i)
                          ? "Remove"
                          : "Add"
                        : "Book now"}
                    </div>
                  </td>
                </tr>
              );
            }
          })}
        </tbody>
      </table>
    </div>
  );
};

export default HBC;
