import React, { useState } from "react";
import style from "./PHC.module.css";
import Accordian from "./Accordian/Accordian";

import { phcList, phcMainList } from "./../PackageDetails";
const PHC = ({ history, handleSelection, options }) => {
  const [searchText, setSearchText] = useState("");
  const formatContent = (obj, objHeading) => {
    var returnData = "<div>";
    if (objHeading.length > 0) {
      let content = objHeading.map((data) => {
        if (obj.hasOwnProperty(data)) {
          returnData = returnData + `<h2>${data}</h2>`;
          obj[data].map((listObj) => {
            returnData =
              returnData +
              `<ul>
            <li>${listObj}</li>
          </ul>`;
          });
        } else {
          returnData = returnData + `<h2>${data}</h2>`;
        }
      });
    } else {
      returnData = returnData + `<ul>`;
      obj["list"].map((obj) => {
        returnData = returnData + `<li>${obj}</li>`;
      });
      returnData = returnData + `</ul>`;
    }

    returnData = returnData + "</div>";
    return returnData;
  };
  return (
    <div className={style.container}>
      {phcMainList.map((data, i) => {
        return (
          <Accordian
            key={i}
            title={data}
            content={formatContent(phcList[data], phcList[data].heading)}
            history={history}
            index={i}
            handleSelection={handleSelection}
            options={options}
          />
        );
      })}
      {/* <Accordian
        title="What is your return policy?"
        content="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."
      />
      <Accordian
        title="What is your return policy?"
        content="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."
      />
      <Accordian
        title="What is your return policy?"
        content="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."
      /> */}
    </div>
  );
};

export default PHC;
