import React, { useState } from "react";
import style from "./Package.module.css";
import PHC from "./PHC/PHC";
import HBC from "./HBC/HBC";
import { Link } from "react-router-dom";
const Package = (props) => {
  const [cal, setCal] = useState(false);
  const [tab, setTab] = useState(1);
  const [tests, setTests] = useState([]);
  const handleSelection = (index) => {
    if (tests.includes(index)) {
      let alteredArray = tests.filter((data) => {
        return data != index;
      });
      setTests(alteredArray);
    } else {
      setTests(tests.concat([index]));
    }
  };
  //console.log(props, "entered");
  return (
    <div className={style.parent_container}>
      <div className={style.package}>
        {tests.length > 0 ? (
          <Link
            className={style.tab_button}
            to={{
              pathname: "/home",
              hash: "#about",
              state: { userData: { type: tab, options: tests } },
            }}
            replace
            // onClick={() => {
            //   //props.history.push("/home", { type: tab, options: tests });
            //   props.history.push({
            //     pathname: "/home#home",
            //     ,
            //   });
            //   props.history.push("/home#about");
            // }}
          >
            Confirm
          </Link>
        ) : (
          <div className={style.tab}>
            <div
              onClick={() => {
                setTab(1);
              }}
              className={
                tab === 1 ? style["tab--active"] : style["tab_element"]
              }
            >
              HOME BLOOD COLLECTION
            </div>
            <div
              onClick={() => {
                setTab(2);
              }}
              className={
                tab === 2 ? style["tab--active"] : style["tab_element"]
              }
            >
              PREVENTIVE HEALTH CHECKUP
            </div>
          </div>
        )}
        <div className={style.subcontainer}>
          {tab === 1 ? (
            <HBC
              history={props.history}
              handleSelection={handleSelection}
              options={tests}
            />
          ) : (
            <PHC
              history={props.history}
              handleSelection={handleSelection}
              options={tests}
            />
          )}
        </div>
      </div>
    </div>
  );
};

export default Package;
