import React from "react";
import style from "./Header.module.css";
import { BrowserRouter as Router, Link, Route, Switch } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPhoneAlt, faMapMarkerAlt } from "@fortawesome/free-solid-svg-icons";
import logo from "../../utils/logo.jpeg";

const Header = () => {
  return (
    <div className={style.container}>
      <div className={style.subcontainer1}>
        <div>
          <FontAwesomeIcon
            style={{ color: "#e8505b", fontSize: "24px" }}
            icon={faMapMarkerAlt}
          />
          <span> Chennai, Selaiyur</span>
        </div>
        <div>
          <FontAwesomeIcon
            style={{ color: "#e8505b", fontSize: "24px" }}
            icon={faPhoneAlt}
          />
          <span>044-48557830</span>
        </div>
      </div>
      <div className={style.subcontainer2}>
        <a href="/home#home">
          <img src={logo} className={style.logo} />
        </a>
        {/* <h1 className={style.logo}>Health Plus</h1> */}
        <input
          type="checkbox"
          id="nav-toggle"
          className={style["nav-toggle"]}
        />
        <nav>
          {" "}
          <div style={{ width: "200px" }}></div>
          <ul htmlFor="nav-toggle">
            <li>
              <a href="/home#home">Home</a>
            </li>
            <li>
              <a href="/home#facility">Facilities</a>
            </li>
            <li>
              <a href="/package">Package</a>
            </li>
            <li>
              <a href="/home#about">About us</a>
            </li>
            <li>
              <a href="#">Blogs</a>
            </li>
          </ul>
        </nav>
        <label htmlFor="nav-toggle" className={style["nav-toggle-label"]}>
          <span></span>
        </label>
      </div>
    </div>
  );
};

export default Header;
